h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}

.is-hidden {
  display: none;
}

@media only screen and (max-width: 1199px) {
  .desk-only {
    display: none;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

button {
  background-color: transparent;
}

a:hover, a:focus {
  text-decoration: none;
}

a {
  color: inherit;
  text-decoration: none;
}

table {
  vertical-align: top;
}

img {
  max-width: 100%;
  vertical-align: top;
}

* {
  -webkit-tap-highlight-color: transparent;
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.search-form__field {
  position: relative;
}

.search-form__field label {
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-form__field input {
  width: 100%;
  height: 40px;
  padding: 0 30px 0 47px;
  font-family: "DINPro-Regular", Arial, sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 40px;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
}

.search-form__field input:-moz-placeholder, .search-form__field input::-moz-placeholder {
  color: #fff;
  opacity: 1;
}

.search-form__field input::-webkit-input-placeholder {
  color: #fff;
}

.search-form_white .search-form__field input {
  border: 1px solid #dbe1e6;
  background-color: #f2f5f7;
  color: #131313;
}

.search-form_white .search-form__field input:-moz-placeholder, .search-form_white .search-form__field input::-moz-placeholder {
  color: #131313;
  opacity: 1;
}

.search-form_white .search-form__field input::-webkit-input-placeholder {
  color: #131313;
}

.search-form__field svg {
  position: relative;
  left: 3px;
  font-size: 16px;
  fill: #fff;
}

.search-form_white .search-form__field svg {
  fill: #131313;
}

.input {
  width: 100%;
  height: 50px;
  padding: 0 23px;
  font-family: "DINPro-Regular", Arial, sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 50px;
  border-radius: 6px;
  border: 1px solid #dbe1e6;
  background-color: #f2f5f7;
}

@media only screen and (max-width: 767px) {
  .input {
    padding: 0 20px;
  }
}

.check-box {
  position: relative;
}

.check-box label {
  display: inline-flex;
  align-items: center;
  font-family: "DINPro-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: #000;
  cursor: pointer;
}

.check-box__square {
  position: relative;
  width: 14px;
  height: 14px;
  border-radius: 2px;
  margin-right: 15px;
  border: 1px solid #dbe1e6;
  background-color: #f2f5f7;
  font-size: 0;
}

.check-box__icon {
  opacity: 0;
  transform: scale(0.6);
  position: absolute;
  top: 1px;
  right: 1px;
  width: 9px;
  height: 9px;
  transition: .3s ease-in-out;
  fill: #131313;
}

.check-box input {
  display: none;
}

.check-box input:checked ~ label .check-box__icon {
  opacity: 1;
  transform: scale(1);
}

.form-field label {
  display: flex;
  align-items: flex-end;
  font-size: 15px;
}

@media only screen and (max-width: 767px) {
  .form-field label {
    flex-direction: column;
    align-items: flex-start;
  }
}

.form-field label span {
  flex: 0 0 100px;
  padding: 0 15px 5px 0;
}

@media only screen and (max-width: 767px) {
  .form-field label span {
    flex: 0 1 auto;
  }
}

.form-field label sup {
  color: #ff0000;
  font-size: 1em;
}

.form-field input {
  height: 40px;
  line-height: 40px;
}
